.block-search {
  padding-bottom: 8px;
  overflow: auto;
  input[type="text"] {
    padding: 8px 25px;
    border: 1px solid #eeeeee;
  }
  input[type="submit"] {
    display: inline-block;
    padding: 8px 20px;
    @media screen and (320px) {
      display: block;
    }
  }

  form {
    text-align: center;
  }

.block-search {
    ul {
      list-style: none;
      margin:0;
      padding:0;
      li{
        border-bottom: 1px solid #eeeeee;
        padding: 15px 15px;
      }
    }
  }
}
